import React, { useEffect, Fragment } from "react";
import DataTable from "react-data-table-component";
import DataTableExtensions from 'react-data-table-component-extensions';

import { connect } from 'react-redux'
import { fecthPosts } from '../redux/actions/postsActions'

const DataTableClientes = ({dispatch, blog}) => {
  
  useEffect(() => {
    dispatch(
      fecthPosts()
    )
  }, [dispatch]);

  const columns = [
    {
      name: "R. SOCIAL",
      selector: "RazonSocial",
      sortable: true
    },
    {
      name: "DOMICILIO",
      selector: "Domicilio",
      sortable: true
    },
    {
      name: "LOCALIDAD",
      selector: "Localidad",
      sortable: true,
    },
    {
      name: "PROVINCIA",
      selector: "Provincia",
      sortable: true
    },
    {
      name: "TELEFONO",
      selector: "Telefono",
      sortable: true
    },
    {
      name: "CUIT",
      selector: "CUIT",
      sortable: true,
    },
    {
      name: "IIBB",
      selector: "IIBB",
      sortable: true
    }
  ];

  const tableData = {
    columns,
    data: blog.posts
  };
  
  const ExpanableComponent = ({ data }) => (
    <table className="uk-table uk-table-middle uk-table-small">
      <tbody className="uk-text-meta uk-text-justify">
          <tr>
              <td>{data.RazonSocial}</td>
              <td>Domicilio {data.Domicilio}</td>
              <td>Localidad {data.Localidad}</td>
              <td>Prov. {data.Provincia}</td>
          </tr>
          <tr>
              <td>Tel. {data.Telefono}</td>
              <td>Cod. Postal {data.CodigoPostal}</td>
              <td>CUIT: {data.CUIT}</td>
              <td>IIBB: {data.IIBB}</td>
          </tr>
      </tbody>
    </table>
  );
  
  return (
    <Fragment>

      { blog.isFetching ? (<span uk-spinner="ratio: 4.5"></span>) :  
      
      <DataTableExtensions className="uk-margin-large-bottom"
        {...tableData}
      >
      <DataTable
        noHeader={true}
        defaultSortField="id"
        defaultSortAsc={false}
        pointerOnHover={true}
        pagination
        highlightOnHover
        expandableRows
        expandableRowsComponent={<ExpanableComponent />}
      />
      </DataTableExtensions>
      }
    </Fragment>
  );
};

export default connect((state) => {
  return state
})(DataTableClientes)